import {coupons} from '@/api/public.js'
import {couponReceive} from '@/api/user.js'
import navT from '../../components/nav/nav.vue'
export default {
	components:{
		navT
	},
	data() {
		return {
			list:[]
		}
	},
	created() {
		this.getCoupons()
	},
	methods: {
		click(id){
			couponReceive({couponId:id}).then(data=>{
				this.getCoupons()
			})
		},
		getCoupons(){
			coupons().then(data=>{
				this.list=data.data.data
				console.log(this.list)
			})
		}
	}
}
